import { h } from "@stencil/core";
export class RaulEyebrow {
    render() {
        return (h("div", { class: "r-eyebrow" },
            h("div", { class: "r-eyebrow-start" }),
            h("div", { class: "r-eyebrow-end" })));
    }
    static get is() { return "raul-eyebrow"; }
    static get originalStyleUrls() { return {
        "$": ["raul-eyebrow.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-eyebrow.css"]
    }; }
}
